html {
	 font-size: 16px;
	 font-family: 'Open Sans', sans-serif;
}
 body {
	 padding: 0;
	 margin: 0;
}
 p {
	 font-size: 1rem;
	 line-height: 1.8rem;
}
 h2 {
	 margin-top: 0;
}
 .white-text {
	 color: #fff;
}
 .button {
	 background: #0d9347;
	 padding: 15px 25px;
	 color: #fff;
	 text-decoration: none;
	 display: inline-block;
	 border: none;
}
 .button.blue {
	 background: #181548;
}
 .container {
	 max-width: 1200px;
	 margin: auto;
	 padding: 0 20px;
}
 .padded {
	 padding-top: 60px;
	 padding-bottom: 60px;
}
 .row {
	 display: flex;
	 flex-wrap: no-wrap;
}
 @media (max-width: 850px) {
	 .row {
		 display: block;
	}
}
 .row.thirds .column {
	 width: 33.3%;
}
 .row.align-bottom {
	 align-items: flex-end;
}
 .column {
	 width: 50%;
}
 @media (max-width: 850px) {
	 .column {
		 width: 100%;
	}
}
 .column.large {
	 width: 70%;
}
 @media (max-width: 850px) {
	 .column.large {
		 width: 100%;
	}
}
 .column.small {
	 width: 30%;
}
 @media (max-width: 850px) {
	 .column.small {
		 width: 100%;
	}
}
 .column.sixty {
	 width: 60%;
}
 @media (max-width: 850px) {
	 .column.sixty {
		 width: 100%;
	}
}
 .column.forty {
	 width: 40%;
}
 @media (max-width: 850px) {
	 .column.forty {
		 width: 100%;
	}
}
 .sub-nav {
	 text-align: right;
}
 .logo {
	 width: 100%;
	 padding: 20px;
	 padding-left: 0;
}
 .logo img {
	 width: 100%;
}
 .menu {
	 list-style: none;
	 padding: 0;
	 display: flex;
	 justify-content: flex-end;
	 align-items: center;
}
 .menu li {
	 padding: 20px;
	 font-weight: bold;
	 color: #181548;
	 transition: 0.4s ease;
	 cursor: pointer;
}
 .menu li:last-child {
	 padding-right: 0;
}
 .menu li:hover {
	 color: #0d9347;
	 transition: 0.4s ease;
}
 .hero {
	 width: 100%;
	 height: 500px;
	 background-position: center;
	 background-size: cover;
}
 .inner-column.left {
	 padding-right: 30px;
}
 @media (max-width: 850px) {
	 .inner-column.left {
		 padding-right: 0;
	}
}
 .inner-column.right {
	 padding-left: 30px;
}
 @media (max-width: 850px) {
	 .inner-column.right {
		 padding-left: 0;
	}
}

/* Form Styles */
 .form-area {
	 position: relative;
}
 .form {
	 position: absolute;
	 width: 100%;
	 background: #0d9347;
	 margin-top: -230px;
	 z-index: 5000;
	 padding: 15px;
}

.nf-form-content label {
    display: none;
}

.nf-form-fields-required {
    display: none;
}

.nf-form-title h3 {
    text-align: center;
    color: #fff;
    text-transform: uppercase;
    font-size: 1.5rem;
    margin: 0;
    margin-bottom: 15px;
}


div#nf-field-4-container {
    margin-bottom: 0;
}

input#nf-field-4 {
    background: #181548;
    width: 100%;
    text-transform: uppercase;
    font-weight: bold;
}

 @media (max-width: 850px) {
	 .form {
		 position: static;
		 margin-top: 0;
	}
}
 #mission {
	 background-size: cover;
	 background-position: center;
	 background-color: #333;
	 background-blend-mode: multiply;
}
 #mission .container {
	 position: relative;
	 z-index: 0;
}
 .featured-icons {
	 text-align: center;
	 position: absolute;
	 right: 0;
	 bottom: -114px;
}
 @media (max-width: 850px) {
	 .featured-icons {
		 position: static;
		 margin-top: 30px;
	}
}
 .featured-icons .column {
	 padding: 10px;
}
 @media (max-width: 850px) {
	 .featured-icons .row {
		 position: static;
		 align-items: center;
		 display: flex;
		 flex-direction: column;
	}
}
 .icon {
	 width: 150px;
	 height: 150px;
	 background: #0d9347;
	 border-radius: 50%;
	 border: 4px solid #fff;
	 margin: auto;
	 position: relative;
}
 .icon::after {
	 content: '';
	 width: 84px;
	 height: 56px;
	 background: #0d9347;
	 position: absolute;
	 bottom: -20px;
	 margin: auto;
	 left: 0;
	 right: 0;
}
 @media (max-width: 850px) {
	 .icon::after {
		 display: none;
	}
}
 .icon svg {
	 position: relative;
	 z-index: 6000;
	 width: 80%;
	 margin: 5%;
	 margin-top: 15px;
}
 #explore {
	 background: #0d9347;
	 border-top: 4px solid #fff;
}
 #explore .container {
	 position: relative;
}
 .half-circle-image {
	 position: absolute;
	 bottom: -65px;
	 right: 0;
}
 @media (max-width: 850px) {
	 .half-circle-image {
		 position: static;
		 margin-bottom: -65px;
	}
}
 .half-circle-image img {
	 width: 100%;
}
 .img-column {
	 position: relative;
}
 footer {
	 width: 100%;
	 background: #333;
	 position: relative;
	 z-index: 5000;
	 border-top: 4px solid #fff;
	 padding: 30px 0;
	 color: #fff;
}
 footer .row {
	 align-items: center;
}
 footer .footer-logo {
	 width: 100%;
}
 footer .footer-logo img {
	 width: 250px;
	 float: right;
}
 @media (max-width: 850px) {
	 footer .footer-logo img {
		 float: none;
	}
}
 footer .footer-icons img {
	 width: 100px;
}

/* CATS Job Postings */

ul.wp-block-rss {
    list-style: none;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
}

li.wp-block-rss__item {
    width: 42%;
    padding: 3%;
    margin: 1%;
    background: lightgray;
}
 